import * as React from 'react'
import styled from '@emotion/styled'
import ThemeTitle from 'components/molecules/theme/ThemeTitle'
import { UserType } from 'types/index'
import { DateTime } from 'luxon'

type Props = {
    users?: UserType[]
}

const Applicant = (props: Props): JSX.Element => {
    const { users } = props

    const usersElm = React.useMemo(() => {
        if (!users) return
        return users.map(user => (
            <UserWrapper key={user.uid}>
                <User>
                    <Icon src={user.photoURL || '/img/icn_default.png'} alt="" />
                    <UserInfo>
                        <UserName>{user.displayName}</UserName>
                        <UpdateDate>{DateTime.fromISO(updateDate).toFormat('yyyy/MM/dd HH:mm:ss')}</UpdateDate>
                    </UserInfo>
                </User>
                <SnsWrapper>
                    <Twitter />
                    <Facebook />
                </SnsWrapper>
            </UserWrapper>
        ))
    }, [])

    return (
        <Wrapper>
            <TitleBox>
                <Title>この募集に応募した人</Title>
                <Lead>
                    頼みたい人に、SNSで
                    <br />
                    直接連絡をとってみましょう。
                </Lead>
            </TitleBox>
            {users && JSON.stringify(users)}
        </Wrapper>
    )
}

export default Applicant

const Wrapper = styled.div``

const TitleBox = styled.div`
    width: 600px;
    margin: 60px auto 0;
    padding: 75px 40px 75px;
    background-color: #fff;
    border-radius: 16px;
    box-sizing: border-box;
    overflow: hidden;
`

const Title = styled(ThemeTitle)`
    margin-bottom: 45px;
`

const Lead = styled.p`
    font-size: 22px;
    line-height: 1.75;
    text-align: center;
`

const UserWrapper = styled.div`
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding-top: 30px;
    border-top: 1px solid #fff;
    box-sizing: border-box;
`

const User = styled.div`
    display: flex;
    align-items: center;
`
const Icon = styled.img`
    display: block;
    width: 67px;
    height: 67px;
    margin-right: 24px;
    border-radius: 50%;
`
const UserInfo = styled.div``
const UserName = styled.p`
    font-size: 22px;
    color: #fff;
`

const UpdateDate = styled.time`
    display: block;
    margin-top: 10px;
    font-size: 18px;
    color: #fff;
`
